import React, { Component } from 'react';
import { connect } from 'react-redux';
import Header from '../../public/shared/Header';
import Footer from '../../public/shared/Footer';
import { Dropdown, Table, Checkbox, Icon, Modal } from 'semantic-ui-react';
import { logActions } from '../../../actions';
import Spinner2 from '../../../utils/Spinner2';

const { fetchLogs, deleteLogs, resetLogStatus } = logActions;

const status = {
  critical: 'red',
  error: 'orange',
  warning: 'yellow',
  info: 'blue'
};

const level = [
  {
    key: 'critical',
    text: 'Critical',
    value: 'critical',
    label: { color: 'red', empty: true, circular: true }
  },
  {
    key: 'error',
    text: 'Error',
    value: 'error',
    label: { color: 'orange', empty: true, circular: true }
  },
  {
    key: 'warning',
    text: 'Warning',
    value: 'warning',
    label: { color: 'yellow', empty: true, circular: true }
  },
  {
    key: 'info',
    text: 'Info',
    value: 'info',
    label: { color: 'blue', empty: true, circular: true }
  }
];

const time = [
  { key: 'last hour', text: 'Last hour', value: 'last hour' },
  { key: 'last 6 hour', text: 'Last 6 hour', value: 'last 6 hours' },
  { key: 'last 24 hour', text: 'Last 24 hour', value: 'last 24 hours' },
  { key: 'last 7 days', text: 'Last 7 days', value: 'last 7 days' }
];

class Logs extends Component {
  state = {
    inputValue: '',
    selectedDevice: 'all',
    selectedLogLevel: 'all',
    selectedTime: 'all',
    checked: false,
    isFilteringLogs: false,
    logsToDisplay: [],
    logsToDelete: [],
    openDeleteModal: false,
    open: false
  };

  componentDidMount() {
    this.props.fetchLogs();
  }

  static getDerivedStateFromProps(props, state) {
    if (props.logs !== null) {
      const {
        logs: { data, status },
        resetLogStatus
      } = props;

      if (status === 'fetched') {
        const {
          resetLogStatus,
          logs: { data }
        } = props;
        resetLogStatus(data);

        const logsToDisplay = data;
        return { logsToDisplay };
      }

      if (status === 'deleted') {
        resetLogStatus(data);
        return {
          open: false,
          logsToDelete: [],
          logsToDisplay: data,
          selectedDevice: 'all',
          selectedLogLevel: 'all',
          selectedTime: 'all',
          inputValue: ''
        };
      }
    }

    return null;
  }

  resetTableScroll() {
    var checkboxes = document.getElementsByName('log');

    for (var i in checkboxes) {
      if (checkboxes[i].checked === true) checkboxes[i].checked = false;
    }

    let tbody = document.getElementsByClassName('logs-tbody')[0];
    if (tbody) tbody.scrollIntoView(true);

    this.setState({ checked: false });
  }

  handleSearch = e =>
    this.setState({ inputValue: e.target.value }, () => this.filterResults());

  updateDeviceFilter = (event, data) =>
    this.setState({ selectedDevice: data.value }, () => this.filterResults());

  updateLogLevelFilter = (event, data) =>
    this.setState({ selectedLogLevel: data.value }, () => this.filterResults());

  updateTimeFilter = (event, data) => {
    let endDate = new Date().getTime();
    let startDate;

    switch (data.value) {
      case 'last hour':
        const oneHour = 1 * 60 * 60 * 1000;
        startDate = new Date().getTime() - oneHour;
        break;
      case 'last 6 hours':
        const sixHours = 6 * 60 * 60 * 1000;
        startDate = new Date().getTime() - sixHours;
        break;
      case 'last 24 hours':
        const twentyFourHours = 24 * 60 * 60 * 1000;
        startDate = new Date().getTime() - twentyFourHours;
        break;
      case 'last 7 days':
        const sevenDays = 7 * 24 * 60 * 60 * 1000;
        startDate = new Date().getTime() - sevenDays;
        break;
      default:
        return this.setState({ selectedTime: 'all' }, () =>
          this.filterResults()
        );
    }

    this.setState({ selectedTime: data.value, endDate, startDate }, () =>
      this.filterResults()
    );
  };

  handleCheck = e => {
    var { logsToDelete } = this.state;
    const id = e.target.id;

    if (e.target.checked) {
      logsToDelete.push(id);
    } else {
      logsToDelete = logsToDelete.filter(_id => _id !== id);
    }

    this.setState({ logsToDelete });
  };

  checkAll = (event, data) => {
    let { logsToDelete } = this.state;
    const checked = data.checked;

    var checkboxes = document.getElementsByName('log');

    if (checked) {
      for (var i in checkboxes) {
        if (checkboxes[i].checked === false) {
          let id = checkboxes[i].id;
          logsToDelete.push(id);
          checkboxes[i].checked = true;
        }
      }
    } else {
      for (var j in checkboxes) {
        if (checkboxes[j].checked === true) checkboxes[j].checked = false;
      }
      logsToDelete = [];
    }

    this.setState({ logsToDelete, checked });
  };

  deleteLogs = () => {
    const { deleteLogs } = this.props;
    const { logsToDelete } = this.state;

    this.setState({ open: true, openDeleteModal: false, checked: false });
    deleteLogs(logsToDelete);
  };

  openModal = () => this.setState({ open: true });
  openDeleteModal = () => this.setState({ openDeleteModal: true });
  closeDeleteModal = () => this.setState({ openDeleteModal: false });

  renderModal() {
    const { open } = this.state;
    return (
      <Modal dimmer='inverted' open={open}>
        <Spinner2 style={{ margin: '0px auto' }} />
      </Modal>
    );
  }

  renderDeleteModal() {
    const { openDeleteModal } = this.state;
    const header = 'Delete logs';
    const content = 'Are you sure you want to delete your logs?';
    const actions = (
      <div>
        <button onClick={this.closeDeleteModal} className='ui button'>
          Cancel
        </button>
        <button onClick={this.deleteLogs} className='ui button delete'>
          Delete
        </button>
      </div>
    );

    return (
      <Modal
        dimmer={true}
        open={openDeleteModal}
        onClose={this.closeModal}
        size='small'
      >
        <Modal.Header>{header}</Modal.Header>
        <Modal.Content>{content}</Modal.Content>
        <Modal.Actions>{actions}</Modal.Actions>
      </Modal>
    );
  }

  renderContent() {
    return (
      <div className='ui basic segment logs'>
        <div className='ui basic segment'>
          <div className='ui grid'>
            <div className='one wide column'></div>
            <div className='fourteen wide column'>
              <div className='ui segment'>{this.renderFilters()}</div>
            </div>
            <div className='one wide column'></div>
          </div>
        </div>
        <div className='ui basic segment'>
          <div className='ui grid'>
            <div className='one wide column'></div>
            <div className='fourteen wide column'>{this.renderTable()}</div>
            <div className='one wide column'></div>
          </div>
        </div>
      </div>
    );
  }

  renderFilters() {
    return (
      <div>
        {this.renderInputFilter()}
        <div className='ui grid'>
          <div className='four wide column' style={{ minWidth: '200px' }}>
            {this.renderDeviceFilterDropdown()}
          </div>
          <div className='four wide column' style={{ minWidth: '200px' }}>
            {this.renderLogLevelFilterDropdown()}
          </div>
          <div className='four wide column' style={{ minWidth: '200px' }}>
            {this.renderTimeFilterDropdown()}
          </div>
        </div>
      </div>
    );
  }

  renderInputFilter() {
    const { inputValue: value } = this.state;

    return (
      <div className='ui fluid icon input logs'>
        <input
          type='text'
          value={value}
          onChange={this.handleSearch}
          placeholder='Filter by label or text search'
        />
        <i className='search icon'></i>
      </div>
    );
  }

  renderDeviceFilterDropdown() {
    const { devices } = this.props;
    const { selectedDevice } = this.state;

    const deviceIds = devices.map(device => {
      const option = {
        key: device._id,
        text: device.id,
        value: device.id
      };

      return option;
    });

    const options = [
      { key: 'all', text: 'All devices', value: 'all' },
      ...deviceIds
    ];

    return (
      <Dropdown
        fluid
        selection
        options={options}
        onChange={this.updateDeviceFilter}
        value={selectedDevice}
      />
    );
  }

  renderLogLevelFilterDropdown() {
    const { selectedLogLevel } = this.state;

    const options = [{ key: 'all', text: 'All types', value: 'all' }, ...level];

    return (
      <Dropdown
        fluid
        selection
        options={options}
        onChange={this.updateLogLevelFilter}
        value={selectedLogLevel}
      />
    );
  }

  renderTimeFilterDropdown() {
    const { selectedTime } = this.state;

    const options = [{ key: 'all', text: 'No limit', value: 'all' }, ...time];

    return (
      <Dropdown
        fluid
        selection
        options={options}
        onChange={this.updateTimeFilter}
        value={selectedTime}
      />
    );
  }

  filterResults() {
    const { data } = this.props.logs;
    const {
      inputValue,
      selectedDevice,
      selectedLogLevel,
      selectedTime
    } = this.state;
    this.setState({ isFilteringLogs: true });

    const filteredResult = data.filter(log => {
      if (selectedDevice !== 'all') {
        if (log.data.deviceId !== selectedDevice) return false;
      }

      if (selectedLogLevel !== 'all') {
        const levelIsDifferent =
          log.data.level.toUpperCase() !== selectedLogLevel.toUpperCase();

        if (levelIsDifferent) return false;
      }

      if (selectedTime !== 'all') {
        const { startDate, endDate } = this.state;

        const logCreatedAt = new Date(log.data.date).getTime();
        const isInTimeRange = logCreatedAt >= startDate && endDate;
        if (!isInTimeRange) return false;
      }

      const match = log.data.message
        .toUpperCase()
        .includes(inputValue.toUpperCase());

      if (!match) return false;

      return true;
    });

    this.setState({ logsToDisplay: filteredResult, logsToDelete: [] }, () => {
      this.resetTableScroll();
      this.setState({ isFilteringLogs: false });
    });
  }

  renderTable() {
    const { checked, logsToDisplay } = this.state;

    if (logsToDisplay.length === 0)
      return (
        <div className='ui segment' style={{ marginBottom: '300px' }}>
          <p
            style={{
              fontStyle: 'oblique',
              textAlign: 'center',
              padding: '30px 0px'
            }}
          >
            No entries found matching current filter.
          </p>
        </div>
      );

    const deleteButton = () => {
      const { logsToDelete } = this.state;

      if (logsToDelete.length === 0) {
        return (
          <div
            className='ui right aligned basic segment'
            style={{ margin: '0px', padding: '0px 0px 10px' }}
          >
            <button className='ui disabled button delete'>Delete</button>
          </div>
        );
      }

      return (
        <div
          className='ui right aligned basic segment'
          style={{ margin: '0px', padding: '0px 0px 10px' }}
        >
          <button onClick={this.openDeleteModal} className='ui button delete'>
            Delete
          </button>
        </div>
      );
    };

    const rows = logsToDisplay.map(log => {
      const data = log.data;
      const levelClassName = `ui ${status[data.level]} empty circular label`;
      const isCritical = data.level === 'critical' ? true : false;

      return (
        <Table.Row key={log._id} negative={isCritical ? true : false}>
          <Table.Cell style={{ paddingLeft: '25px' }}>
            <div className={levelClassName}></div>
          </Table.Cell>
          <Table.Cell>{data.deviceId}</Table.Cell>
          <Table.Cell>
            {isCritical ? <Icon name='attention' /> : ''}
            {data.message}
          </Table.Cell>
          <Table.Cell>{data.date}</Table.Cell>
          <Table.Cell textAlign='right'>
            <input
              className='logs-checkbox'
              type='checkbox'
              id={log._id}
              name='log'
              onChange={this.handleCheck}
            />
          </Table.Cell>
        </Table.Row>
      );
    });

    return (
      <React.Fragment>
        {deleteButton()}
        <div className='table-wrapper'>
          <Table selectable padded>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Level</Table.HeaderCell>
                <Table.HeaderCell>Device ID</Table.HeaderCell>
                <Table.HeaderCell>Message</Table.HeaderCell>
                <Table.HeaderCell>Date</Table.HeaderCell>
                <Table.HeaderCell textAlign='right'>
                  <Checkbox
                    onChange={this.checkAll}
                    style={{ marginRight: '2px' }}
                    checked={checked}
                  />
                </Table.HeaderCell>
              </Table.Row>
            </Table.Header>

            <Table.Body className='log-tbody'>{rows}</Table.Body>
          </Table>
        </div>
      </React.Fragment>
    );
  }

  render() {
    const { logs } = this.props;

    if (logs === null) {
      return (
        <div>
          <div className='dashboard-logs'>
            <div
              className='ui basic segment'
              style={{ margin: '0px', padding: '120px 14px 30px' }}
            >
              <div className='ui basic segment'>
                <div className='ui grid'>
                  <div className='three wide column'></div>
                  <div className='four wide column'>
                    <h2 className='your-devices'>Logs</h2>
                  </div>
                  <div className='nine wide column'></div>
                </div>
              </div>
            </div>
          </div>
          <div className='ui basic segment logs' style={{ height: '90vh' }}>
            <Spinner2 style={{ marginTop: '400px' }} />
          </div>
        </div>
      );
    }

    return (
      <div className='dashboard-logs'>
        {this.renderModal()}
        {this.renderDeleteModal()}
        <Header logo={false} dashboard={true} />
        <div
          className='ui basic segment'
          style={{ margin: '0px', padding: '120px 14px 30px' }}
        >
          <div className='ui basic segment'>
            <div className='ui grid'>
              <div className='three wide column'></div>
              <div className='four wide column'>
                <h2 className='your-devices'>Logs</h2>
              </div>
              <div className='nine wide column'></div>
            </div>
          </div>
        </div>
        {this.renderContent()}
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return { devices: state.devices.data, logs: state.logs };
};

export default connect(mapStateToProps, {
  fetchLogs,
  deleteLogs,
  resetLogStatus
})(Logs);
